<template>
  <div class="articleList">
    <a-list
    itemLayout="vertical"
    size="large"
    :dataSource="articleList">
      <a-list-item slot="renderItem" slot-scope="item" key="item.title">
        <template slot="actions">
          <span>
            <a-icon type="like-o" style="margin-right: 8px" />
            {{item.like}}
          </span>
          <span>
            <a-icon type="fire" style="margin-right: 8px" />
            {{item.traffic}}
          </span>
          <span style="color: #aaa;">By&ensp;{{item.createBy}}</span>
        </template>
        <img slot="extra" width="160" alt="logo" src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png" />
        <a-list-item-meta
          :description="item.summary"
        >
          <a @click="viewArticle(item.id)" slot="title">{{item.title}}</a>
          <!-- <a-avatar slot="avatar" :src="item.avatar" /> -->
        </a-list-item-meta>
        
      </a-list-item>
    </a-list>
  </div>
</template>

<script>
// import { Button, Tag, Icon, List} from 'ant-design-vue';

export default {
  props: [
    'articleList'
  ],
  components: {
    // 'a-button': Button,
    // 'a-tag': Tag,
    // 'a-icon': Icon,
    // 'a-list': List,
    // 'a-list-item': List.Item,
    // 'a-list-item-meta': List.Item.Meta
  },
  data() {
    return {
      actions: [
        { type: 'like-o', text: '156' },
        { type: 'message', text: '2' },
      ]
    }
  },
  methods: {
    viewArticle(id) {
      this.$router.push(`/article/${id}`)
    }
  }
}
</script>

<style>
.articleList {
  margin-top: 3rem;
}
</style>



